// webpackvmc
require('./styles/vmc.less');

(function() {
    var vmc = require('./index');
    var install = require('./components/install');

    if (window.Vue) {
        var Vue = window.Vue;
        Vue.use(install);

        Object.keys(vmc)
            .forEach(key => {
                if (key !== 'Utils') {
                    Vue.component(key, vmc[key]);
                }
            })
    }

    vmc.version = process.env.VERSION;

    if (exports === 'object' && typeof module !== 'undefined') {
        module.exports = vmc;
    } else {
        window.VMC = vmc;
    }
}());


